.page-header {
  display: flex;
  flex-wrap: wrap;
  margin: 0 auto;
  margin-bottom: 270px;

  @media (max-width: $tablet-width) {
    margin-bottom: 117px;
  }
}

.page-header__logo-img {
  width: 202px;
  height: 52px;

  @media (max-width: $mobile-width) {
    width: 32px;
    height: 32px;
  }
}
